<template>
  <div class="cards">
    <el-card
      style="width: 100%"
      class="card"
      v-for="(item, index) in data"
      :key="index"
    >
      <template #header>
        <div class="card-header" @click="route_to_post(item.basename)">
          <span>{{ item.title }}</span>
        </div>
      </template>
      <div class="card-context">
        <article v-html="markdown_to_html(item.abstract)"></article>
      </div>
      <div class="card-footer">
        <div class="tags">
          <el-tag class="tag">#{{ item.date }}</el-tag>
          <el-tag class="tag" v-for="(tags, index2) in item.tags" :key="index2"
            >#{{ tags }}</el-tag
          >
        </div>
        <el-button
          type="text"
          class="button"
          @click="route_to_post(item.basename)"
          >查看全文</el-button
        >
      </div>
    </el-card>
  </div>
</template>
<script>
import axios from "axios";
import markdown from "marked";
export default {
  // 根据page_now去跟新页面
  // 几乎可以全部复用，到时候再改
  data() {
    return {
      data: [],
    };
  },
  methods: {
    set_page_total() {
      const url =
        this.$store.state.base_url +
        `/tags/${this.$route.params.tag_name}/total_number.txt`;
      axios.get(url).then((response) => {
        this.$store.state.page_total = parseInt(response.data, 10);
      });
    },
    get_page_data() {
      const url =
        this.$store.state.base_url +
        `/tags/${this.$route.params.tag_name}/page_${this.$store.state.page_now}.json`;
      axios.get(url).then((response) => {
        console.log(response.data);
        this.data = response.data;
      });
      // 回到顶
      document.getElementsByTagName("html")[0].scroll(0, 0);
    },
    markdown_to_html(conetxt) {
      return markdown(conetxt);
    },
    route_to_post(name) {
      this.$router.push({ name: "post", params: { post_name: name } });
    },
  },
  computed: {
    page_now() {
      return this.$store.state.page_now;
    },
  },
  watch: {
    page_now() {
      console.log("tag store change");

      this.get_page_data();
    },
  },
  mounted() {
    document.title = this.$store.state.base_title + this.$route.params.tag_name;
    this.$store.state.page_now = 1;
    this.set_page_total();
    this.get_page_data();
  },
};
</script>
